import React, { useEffect, useState } from 'react';
import { Button, Col, Container, Row, Table } from 'reactstrap';
import { Rep } from '../Repetition/Rep';

export const Serie = ({ title, data, min, max }) => {
  const [_data, setData] = useState(data);

  useEffect(() => {
    setData(
      _data.map(item => {
        return { ...item, editable: { value: true } };
      }),
    );
    console.log(_data);
  }, []);

  const handleOnSave = () => {
    console.log('saving');
  };

  const handleOnAdd = () => {
    if (_data.length < max) {
      setData([
        ..._data,
        { ..._data[_data.length - 1], id: { value: _data.length + 1 }, editable: { value: true } },
      ]);
    } else console.log('That is the max!!!');
  };

  const handleOnDelete = () => {
    if (_data.length > min) {
      setData(_data.filter(item => item.id.value !== _data.length));
    } else console.log('That is the min!!!');
  };

  const onItemChange = item => {
    console.log(item);
    setData(
      _data.map(serie => {
        if (serie.id.value === item.id.value) {
          return item;
        }
        return serie;
      }),
    );
  };

  return (
    <Container>
      <Row>
        <h2>{title}</h2>
      </Row>
      <Row>
        <Table>
          <thead>
            <tr>
              {Object.keys(data[0]).map(c => (
                <th key={c}>{c}</th>
              ))}
            </tr>
          </thead>
          <tbody>
            {_data.map(item => (
              <Rep key={item.id.value} item={item} onItemChange={onItemChange}></Rep>
            ))}
          </tbody>
        </Table>
      </Row>
      <Row>
        <Col xs={8}>
          <Button color="success" onClick={handleOnSave}>
            Save
          </Button>
        </Col>
        <Col xs={4}>
          <Button color="danger" onClick={handleOnDelete}>
            -
          </Button>
          <Button color="primary" onClick={handleOnAdd}>
            +
          </Button>
        </Col>
      </Row>
    </Container>
  );
};
